import React, { Component } from 'react';
import './app.css';
import Header from '../header/header';
import Search from '../search/search';
import Filter from '../filter/filter';
import List from '../list/list';
import AddForm from '../addForm/addForm';

export default class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      data: [
        {
          label: 'Going to learn React',
          important: false,
          like: false,
          id: 1,
        },
        {
          label: 'That`s good',
          important: false,
          like: false,
          id: 2,
        },
      ],
      term: '',
      filter: 'all',
    };
  }

  deleteItem = (id) => {
    const newData = this.state.data.filter((item) => item.id !== id);
    this.setState({ data: newData });
  };

  addItem = (body) => {
    if (body.length === 0) {
      return;
    }
    const post = {
      label: body,
      important: false,
      id: Math.random(),
    };

    const posts = [...this.state.data];
    posts.push(post);

    this.setState({ data: posts });
  };

  onToggleImportant = (id) => {
    const newPosts = [...this.state.data];
    const idx = newPosts.findIndex((item) => item.id === id);
    newPosts[idx].important = !newPosts[idx].important;
    this.setState({ data: newPosts });
  };

  onToggleLiked = (id) => {
    const newPosts = [...this.state.data];
    const idx = newPosts.findIndex((item) => item.id === id);
    newPosts[idx].like = !newPosts[idx].like;
    this.setState({ data: newPosts });
  };

  searchPost = (items, term) => {
    if (term.length === 0) {
      return items;
    }

    return items.filter((item) => {
      return item.label.toLowerCase().indexOf(term.toLowerCase()) > -1;
    });
  };

  filterPosts = (items, filter) => {
    if (filter === 'like') {
      return items.filter((item) => item.like);
    } else {
      return items;
    }
  };

  onUpdateSearch = (term) => {
    this.setState({ term });
  };

  onFilterSelect = (filter) => {
    this.setState({ filter });
  };

  render() {
    const { data, term, filter } = this.state;

    const likedPosts = this.state.data.filter((item) => item.like).length;
    const allPosts = this.state.data.length;

    const visiblePosts = this.filterPosts(this.searchPost(data, term), filter);

    return (
      <div className='app container'>
        <Header likedPosts={likedPosts} allPosts={allPosts} />
        <div className='search-panel d-flex'>
          <Search onUpdateSearch={this.onUpdateSearch} />
          <Filter filter={filter} onFilterSelect={this.onFilterSelect} />
        </div>
        <List
          posts={visiblePosts}
          onDelete={this.deleteItem}
          onToggleImportant={this.onToggleImportant}
          onToggleLiked={this.onToggleLiked}
        />
        <AddForm onAdd={this.addItem} />
      </div>
    );
  }
}
